body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: aliceblue;
}

* {
  box-sizing: border-box;
}

html,
body,
#root {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
}

canvas{
  display: block;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.buttonMenu{
    position: fixed;
    top: 15px;
    left: 15px;
    z-index: 10000;
}

.sidenav {
  height: 100%;
  width: 0;
  position: fixed;
  z-index: 10001;
  top: 0;
  left: 0;
  background-color: #111;
  overflow-x: hidden;
  transition: 0.2s;
  padding-top: 60px;
}

.sidenav a {
  padding: 8px 8px 8px 32px;
  text-decoration: none;
  font-size: 25px;
  color: #818181;
  display: block;
  transition: 0.2s;
}

.sidenav a:hover {
  color: #f1f1f1;
}

.sidenav .closebtn {
  position: absolute;
  top: 0;
  right: 25px;
  font-size: 36px;
  margin-left: 50px;
}

@media screen and (max-height: 450px) {
  .sidenav {padding-top: 15px;}
  .sidenav a {font-size: 18px;}
}

#place-button{
    position: absolute;
    bottom: 20px;
    left: calc(50% - 50px);
    width: 100px;
    height: 35px;
    display: none;
}
#group-zoom{
    position: fixed;
    top: 20px;
    right: 20px;
    display: none;
    z-index: 10000;
}
#zoom-in-button, #zoom-out-button, #hide-button{
    min-width: 50px;
    height: 35px;
    display: block;
    margin-bottom: 10px;
    font-size: 16px;
    margin-left: auto;
}

#loader-model{
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #0000008f;
  z-index: 100000000;
  display: none;
}
#loader-model span{
  color: #fff;
  display: block;
  text-align: center;
  margin-top: 50px;
  font-size: 20px;
}
